<template>
  <div>
    <div class="content">
      <p class="logo">
        <!-- <img src="@assets/images/crm.png" /> -->
      </p>
      <P class="crm">修改密码</P>
      <p>
        <input type="text" v-model="account" placeholder="手机号码" class="account" value />
      </p>
      <p>
        <input type="password" v-model="opassword" placeholder="旧密码" class="password" value />
      </p>
      <p>
        <input type="password" v-model="npassword" placeholder="新密码" class="password" value />
      </p>
      <p>
        <input type="password" v-model="rpassword" placeholder="重复密码" class="password" value />
      </p>
      <div class="submit" @click="submitLogin">确认修改</div>

      <router-link class="forgetPassword" :to="{ path: '/login'}">去登陆</router-link>
    </div>
    <p class="bottom">*若没有账号,请联系管理员*</p>
  </div>
</template> 
<script>
import { forgetPassword } from "@api/user";
import { Checkbox, CheckboxGroup } from "vant";
import cookie from "@utils/store/cookie";
import { trim } from "@utils";

export default {
  name: "修改密码",
  components: {
    [Checkbox.name]: Checkbox,
    [CheckboxGroup.name]: CheckboxGroup,
  },
  data: function () {
    return {
      checked: true,
      account: "",
      opassword: "",
      npassword: "",
      rpassword: "",
    };
  },
  mounted: function () {},
  methods: {
    submitLogin() {
      if (trim(this.account) == "") {
        this.$dialog.message("请输入手机号");
      } else if (trim(this.opassword) == "") {
        this.$dialog.message("请输入旧密码");
      } else if (trim(this.npassword) == "") {
        this.$dialog.message("请输入新密码");
      } else if (trim(this.rpassword) != trim(this.npassword)) {
        this.$dialog.message("两次密码不一致");
      } else {
        forgetPassword({
          mobile: this.account,
          newPassword: this.npassword,
          oldPassword: this.opassword, 
          passwordCopy: this.rpassword,
        })
          .then(({ data }) => {
            this.$dialog.message("修改成功");
            this.$router.replace({ path: "/" });
          })
          .catch((e) => {
            this.$dialog.message(e.msg);
          });
      }
    },
  },
};
</script>
