<template>
  <div>
    <div class="content">
      <p class="logo">
        
      </p>
      <p class="crm">华晟包装</p> 
      <p>
        <input type="text" v-model="account" placeholder="手机号码" class="account" value />
      </p>
      <p>
        <input type="password" v-model="password" placeholder="密码" class="password" value />
      </p>
      <div class="check flex-fs">
        <van-checkbox v-model="checked" icon-size="14px" shape="square">记住密码</van-checkbox>
      </div>
      <div class="submit" @click="submitLogin">登录</div>

      <router-link class="forgetPassword" :to="{ path: '/change_password'}">修改密码?</router-link>
    </div>
    <p class="bottom">*若没有账号,请联系管理员*</p>
  </div>
</template>
<script>
import { login } from "@api/user";
import { Checkbox, CheckboxGroup } from "vant";
import localStorage from "@utils/store/localStorage";
import { trim } from "@utils";

export default {
  name: "登录",
  components: {
    [Checkbox.name]: Checkbox,
    [CheckboxGroup.name]: CheckboxGroup,
  }, 
  data: function () {
    return {
      checked: true,
      account: "",
      password: "",
    };
  },
  created() {

    if (localStorage.get("userInfo") && localStorage.get("ntoken")) {
      this.$router.replace({ path: "/index" });
    }
  },
  mounted: function () {
    var account = localStorage.get("account_m");
    var password = localStorage.get("account_p");
    if (account && password) {
      password = password.replace("x19980731", "");
      password = password.replace("!857094/", "");
      this.account = account;
      this.password = password;
    }
  },
  methods: {
    submitLogin() {
       
      if (trim(this.account) == "") {
  
        this.$dialog.message('请输入手机号');
      } else if (trim(this.password) == "") {
        this.$dialog.message("请输入密码");
      } else {
        login({ mobile: this.account, password: this.password }) 
          .then(({ data }) => { 
         
            localStorage.set("ntoken", data.token);
            localStorage.set("userInfo", JSON.stringify(data));
            if (this.checked) {
              localStorage.set("account_m", this.account);
              localStorage.set(
                "account_p",
                "!857094/" + this.password + "x19980731"
              );
            }
             this.$router.replace({ path: "/index" });
             
          })
          .catch((e) => {
            this.$dialog.message(e.msg);
          });
      }
    },
  },
};
</script>
